body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #007aff;
}

a:hover {
  color: #0055ff;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
  color: #333;
}

p {
  color: #666;
}

img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.container {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

header {
  background-color: #3498db;
  color: #fff;
  padding: 20px;
  text-align: center;
}

header h1 {
  font-size: 24px;
  margin: 0;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

nav li {
  margin-right: 20px;
}

nav a {
  color: #fff;
  text-decoration: none;
}

nav a:hover {
  color: #ccc;
}

.articoli {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.articolo {
  margin: 20px;
  padding: 20px;
  background-color: #f7f7f7;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: calc(50% - 20px);
}

.ia-chat {
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 20px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 300px;
}


